window.addEventListener('load', function () {

    // mobile menu
    $('.menuBtn').on('click', function(){
		$('.nav_menu').addClass('activeMenu');
    });
    $('.close_menu').on('click', function(){
		$('.nav_menu').removeClass('activeMenu');
    });
    
    // mobile menu end

    // header slider
    $('.fade').slick({
        dots: true,
        infinite: true,
        speed: 700,
        fade: true,
        cssEase: 'linear',
        autoplay: true,
        autoplaySpeed: 3000,
        arrows: false
    });

    // header slider end

    // broadcast slider

    $('.multiple-items').slick({
        infinite: true,
        slidesToShow: 7,
        slidesToScroll: 7,
        dots: false,
        arrows: false,
        cssEase: 'linear',
        speed: 700
    });

    // broadcast slider end

    // fixed menu
    window.onscroll = function() {myFunction()};
	var header = document.querySelector('nav');
	var sticky = header.offsetTop;
	var mainn = document.querySelector('main');
	function myFunction() {
		if (window.pageYOffset > sticky) {
			header.classList.add('sticky');
			mainn.style.marginTop = '57px';
        } 

        else if(header.classList.contains('nofixNav')){
            header.classList.remove('sticky');
			mainn.style.marginTop = '0px';
        }

        else {
			header.classList.remove('sticky');
			mainn.style.marginTop = '0px';
		}
    }
    // fixed menu end

    // up

    var $btn = $('.btn-up');
    var h = 300;
    var btnShowed = false;
    
    function onScrollEnd(){
        var scrollTop = $(this).scrollTop();
        
        if(!btnShowed && scrollTop >= h){
            $btn.fadeIn(20);
            btnShowed = true;
        }
        else if(btnShowed && scrollTop < h){
            $btn.fadeOut(20);
            btnShowed = false;
        }
    }
    
    var scrollTimer;
    
    $(document).on('scroll', function(){
        var document = this;
        
        if(scrollTimer !== undefined){
            clearInterval(scrollTimer);
        }
        
        scrollTimer = setTimeout(function(){
            onScrollEnd.call(document);
        }, 200);
    });

    $btn.on('click', function(){
        $('html, body').animate({
            scrollTop: 0
        }, 700);
    });

    
    onScrollEnd.call(document);

    // up end

    // wow 
    wow = new WOW(
        {
          boxClass: 'wow',      // default
          animateClass: 'animated', // default
          offset: 0,          // default
          mobile: false,       // default
          live: true        // default
        }
    )
    wow.init();

    // wow end
});